.navbar {
    background-color: black;
    padding: 10px 0;
    border-bottom: 1px solid white;
}

.navbar ul{
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

.navbar ul li{
    padding: 0 15px;
    display: inline-block;
}

.navbar ul li a{
    text-decoration: none;
    font-weight: bold;
    color: white;
}